import { Component, h, Element, Prop, State, Watch } from '@stencil/core';
import priceFormat from '../../../global/functions/price';

@Component({
  tag: 'ks-cart-product-price',
  styleUrl: 'cartProductPrice.css'
})
export class CartProductPrice {

  @Prop() price: number;
  @Prop() amount: number;
  @Prop() shippingTime: string = "";
  @Prop() editLink: string = "";
  @Prop() productTableTotal: string = "";
  @Prop() backToCart: string = "Wróć do koszyka";

  @Prop() loading: number = 0;
  @State() loadingDelayed: boolean = false;
  loadingTimeout: number;
  @Watch('loading') LoadingWatcher() {
    if(this.loading) {
      this.loadingTimeout = setTimeout(() => {
        this.loadingDelayed = true;
      }, 300);
    }

    else {
      clearTimeout(this.loadingTimeout);
      this.loadingDelayed = false;
    }
  }

  @Element() root: HTMLElement;

  componentDidLoad() { 
    this.root.style.display = "block";
    this.LoadingWatcher();
  }

  

  render() {
    const price = priceFormat(this.price);

    return[
        <div>
          <div class="sentence ks-text-decorated">
              {this.loadingDelayed && this.loading ? 
                <div class="uk-animation-fade" uk-spinner="ratio: 0.7"></div>: [

                <span>{this.productTableTotal} <span class="price">{price}</span></span>,
                this.shippingTime != "" ? <span class="shipping">{this.shippingTime}</span> : null
              ]}
          </div>

          {this.editLink ?
            <div class="edit">
              <a href={this.editLink} class="uk-button uk-button-default uk-width-1-1">
                {this.backToCart}
              </a>
            </div>
          :null }
        </div>,
        <hr />
    ];
  }
}
